import React, { Component } from 'react'
import './Company.css'
import {Link, withRouter} from 'react-router-dom'
import axios from 'axios'
import { API_URL } from '../../services/url';
import authService from '../../services/auth-service';
import axiosService from '../../services/axios-service';
import $ from 'jquery';
export class ViewCompany extends Component {
    constructor(props)
    {
        super(props)
        this.state = {
            userdata : [],
            name:"",
            _id:"",
            contact_search : '',
            currentPage: 1,
            todosPerPage: 15
        }
        this.edithandler = this.edithandler.bind(this)
        this.deletehandler = this.deletehandler.bind(this)
        this.contactsearchhandle = this.contactsearchhandle.bind(this)
        this.handlepagenumber = this.handlepagenumber.bind(this)
    }
    handlepagenumber(event) {
      this.setState({
        currentPage: Number(event.target.id)
      });
    } 
    componentDidMount() {
        this.handleclick();
    }
    handleclick() {        
        console.log('website on load content component did mount')
               axios.get(`${API_URL}/company/list`,axios.defaults.headers.common['w_auth'] = authService.getToken())
               .then(response => {
                       console.log(response.data);
                       const data = response.data;
                       this.setState({ userdata: data })
                       }) .catch(error => {
                       //console.log(error);
                       })

    }
    change = e => {
      this.setState ({
          [e.target.name] : e.target.value
      })
    }
    
    async contactsearchhandle () {
      console.log('search here ',this.state.contact_search)
      if(this.state.contact_search.length < 10)
      {
        console.log('Please Enter Valid Mobile Number')
      }
      else
      {
        const dataNumber=this.state.contact_search;
        console.log(dataNumber);
        
      //const response= await axios.get(`${API_URL}/company/list`,{ data: { contactNumber: dataNumber } },axios.defaults.headers.common['w_auth'] = authService.getToken()  )
        // .then(response => {
        //         console.log(response.data);
        //         // const data = response.data;
        //         // this.setState({ userdata: data })
        //         })
        //          .catch(error => {
        //         //console.log(error);
        //         })
        //console.log(response);

      }
    }
    
    edithandler ()
    {

    }
   async deletehandler (todo)
    {
     await this.setState({_id:todo._id,
      });
      console.log(this.state._id);
      // console.log(item);
    const response = await axios.delete(`${API_URL}/company/delete`,{ data: { _id: todo._id } }, axios.defaults.headers.common['w_auth'] = authService.getToken() )
        console.log(response)
        if (response.data.n===1){
          alert("company has been deleted");
          this.handleclick();
          //window.location.reload();
        }
    }

 render() {
  const { todos, currentPage, todosPerPage } = this.state;

  let renderTodos;
  let sr=0;
  // Logic for displaying todos
  const indexOfLastTodo = currentPage * todosPerPage;
  const indexOfFirstTodo = indexOfLastTodo - todosPerPage;
  const currentTodos = this.state.userdata.slice(indexOfFirstTodo, indexOfLastTodo);

  if(this.state.userdata.length === 0 )
  {
    renderTodos = (
      <div>
       <center><h4><strong>There is NO Customer Addedd Yet.</strong></h4></center>
      </div>
    )
  }
  else
  {
   renderTodos = currentTodos.map((todo, _id) => {
     return (
         
      <tr key = {_id}>
      {/* <td>{++sr}</td> */}
      <td>{todo.name || "NO DATA"}</td>
      <td>{todo.city || 'NO DATA'}</td>
      <td>{todo.address || 'NO DATA'}</td>
      <td>{todo.contactNumber || 'NO DATA'}</td>
      <td>{todo.contactPerson || 'NO DATA'}</td>
      <td>
      {/* <button type="button" className="btn btn-warning custom-edit-btn custom-btn" onClick = {this.edithandler.bind(this)}><i className="icon-pencil icons"></i></button> */}
      <button type="button" className="btn btn-danger custom-btn" onClick = {this.deletehandler.bind(this,todo)}><i className="icon-trash icons"></i></button>
      </td>
      </tr>
         
     )
     });
    }

     // Logic for displaying page numbers
     const pageNumbers = [];
     for (let i = 1; i <= Math.ceil(this.state.userdata.length / todosPerPage); i++) {
       pageNumbers.push(i);
     }
 
     const renderPageNumbers = pageNumbers.map(number => {
       return (
         <li
           key={number}
           id={number}
           className="active"
           onClick={this.handlepagenumber}
         >
           {number}
         </li>
       );
     });
   return (
       <React.Fragment>
     <div> 
           <main className="main">
           <ol class="breadcrumb">
       <li class="breadcrumb-item"> <Link to ={process.env.PUBLIC_URL+ "/ViewCategory"}>Home</Link></li>
          <li class="breadcrumb-item">
            View Company
          </li>
          </ol>
               <div className="container-fluid">
                      <div className="row">
                        <div className="col-sm-12">
                          <div className="card custom-company-card-view">
                          <div className="custom-search-here">
                      <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <div className="input-group mb-3">
  <input type="number" className="form-control" placeholder="Enter Contact Here" aria-label="Enter Contact Here" aria-describedby="basic-addon2" name="contact_search" value={this.state.contact_search} onChange={e => this.change(e)}/>
  <div className="input-group-append">
    <button className="btn btn-outline-secondary" type="button" onClick={this.contactsearchhandle.bind(this)}>SEARCH</button>
  </div>
</div>
</div>
                      </div>

                    </div>
                    </div>
                  </div>
                   <div className="row">
                       <div className="col-sm-12">
                       <div className="card custom-company-card-view-content">
                       <div className="row">
                           <div className="offset-lg-8 col-lg-4 offset-md-8 col-md-4">
                             <Link to ={process.env.PUBLIC_URL+"/Company"}> <button type="button" className="btn btn-info custom-add-btn"><i className="icon-plus"></i>&nbsp;&nbsp;Add Company</button> </Link>
                           <br /><br/>
                       </div>
                           </div>
                 {/* <div className="card-header table-bordered">
                   <i className="fa fa-align-justify"></i> Companies</div> */}
                 <div className="card-body">
                   <table className="table table-responsive-sm table-bordered custom-company-taqble-view">
                     <thead className="thead-dark">
                       <tr>
                         {/* <th>Sr. No </th> */}
                         <th>Name</th>
                         <th>City</th>
                         <th>Address</th>
                         <th>Number</th>
                         <th>Contact Person</th>
                         <th>Action</th>
                       </tr>
                     </thead>
                     <tbody>
                        {renderTodos}
                     </tbody>
                   </table>
                   <div className="custom-listitem">
        <ul id="page-numbers">
          {renderPageNumbers}
        </ul>
        </div>
                   {/* <ul className="pagination">
                     <li className="page-item">
                       <a className="page-link" href="#">Prev</a>
                     </li>
                     <li className="page-item active">
                       <a className="page-link" href="#">1</a>
                     </li>
                     <li className="page-item">
                       <a className="page-link" href="#">2</a>
                     </li>
                     <li className="page-item">
                       <a className="page-link" href="#">3</a>
                     </li>
                     <li className="page-item">
                       <a className="page-link" href="#">4</a>
                     </li>
                     <li className="page-item">
                       <a className="page-link" href="#">Next</a>
                     </li>
                   </ul> */}
                 </div>
               </div>
                       </div>
                   </div>
               </div>
           </main>
     </div>
     </React.Fragment>
   )
 }
}
export default withRouter(ViewCompany)
//export default ViewCompany
