import React, { Component } from 'react';
import './Login.css';
import axios from 'axios';
 import authService from '../../services/auth-service';
 //import axiosService from '../../services/axios-service';
import {Redirect} from 'react-router-dom';
import { API_URL } from '../../services/url';

class AddLogin extends Component {
	constructor(props)
	{
		super(props);
		this.state = {
			contact: "",
		  password: ""
		}	
		
	}
	change = e => 
	{
	 this.setState({
		 [e.target.name] : e.target.value
	 })
	}
	onSubmit = e =>
	{
		e.preventDefault();
		this.setState({
		contact : "",
		password: ""
        })
        //console.log(this.state);
        //console.log(`${API_URL}/company/login`);
		axios.post(`${API_URL}/admin/loginDo`,this.state)
        .then(res =>{ 
                    console.log(res.data)
                    if(res.data.loginSuccess){
                    console.log(res.data);
                      authService.saveToken(res.data.w_auth);
                      localStorage.setItem('companyId',res.data.companyId);
                      localStorage.setItem('number',res.data.user.contactNumber);
                      localStorage.setItem('name',res.data.user.name);
                      localStorage.setItem('company_yantra_client', res.data.company[0].name)
                    //  if(res.data.data["kind"] === "login"){
                        alert("Login Successful");
                    // this.props.onRouteChange('home');
                    //return <Redirect to={{pathname: '/'}} />
                   window.location.reload();
                  }else{
                        alert(res.data.message);
				            }
         })
		
		
	}
    render() {
		//const onRouteChange = this.props.onRouteChange;
        return (
            <main className="main">
             <div className="form-example-area custom-form-example">
             <div class="container">
      <div class="row justify-content-center">
        <div class="col-md-8">
          <div class="card-group">
            <div class="card p-4 custom-login-card">
              <div class="card-body">
                <h1>Login</h1>
                <p class="text-muted">Sign In to your account</p>
                <div class="input-group mb-3">
                  <div class="input-group-prepend">
                    <span class="input-group-text">
                      <i class="icon-user"></i>
                    </span>
                  </div>
                  <input class="form-control" type="text" placeholder="Enter Mobile Number" name="contact" value = {this.state.contact} onChange = {e =>this.change(e)} />
                </div>
                <div class="input-group mb-4">
                  <div class="input-group-prepend">
                    <span class="input-group-text">
                      <i class="icon-lock"></i>
                    </span>
                  </div>
                  <input class="form-control" type="password" placeholder="Enter Password" name="password" value = {this.state.password} onChange = { e => this.change(e)} />
                </div>
                <div class="row">
                  <div class="col-6">
                    <button class="btn btn-primary px-4" onClick = {e => this.onSubmit(e)} type="button">Login</button>
                  </div>
                  {/* <div class="col-6 text-right">
                    <button class="btn btn-link px-0" type="button">Forgot password?</button>
                  </div> */}
                </div>
              </div>
            </div>
            {/* <div class="card text-white bg-primary py-5 d-md-down-none" style={{width:"44%"}}>
              <div class="card-body text-center">
                <div>
                  <h2>Sign up</h2>
                  <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
                  <button class="btn btn-primary active mt-3" type="button">Register Now!</button>
                </div>
              </div>
            </div> */}
          </div>
        </div>
      </div>
    </div>
			 </div>
            </main>
        );
    }
}

export default AddLogin;